.site-logo {
	display: block;
	max-width: 40px;
}

@media screen and (min-width: $breakpoint-1) {
	.app-header__branding {
		width: 50%;
	}

	.site-logo {
		display: inline-block;
		margin-right: $global-spacing-unit-1 * 2;
		margin-top: -0.25em;
		max-width: 60px;
	}
}

.title-header {
	background-color: var(--color-base);
	background-image: url("/wp-content/themes/syopajatyo/dist/img/blue-bg.jpg");
	background-position: center 18%;
	background-size: cover;
	background-repeat: no-repeat;
	color: var(--color-white);

	&__bg-img {
		top: $global-spacing-unit-1 * 2;
	}

	&__title {
		position: relative;
		padding-top: $global-spacing-unit-1 * 6;
	}
}
